html, body { height: 100%; }

.parent {
  display: flex;
  justify-content: center;
  align-content: center;
  width: 100%;
  height: 100%;
  background-color: whitesmoke;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.col {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.title {
  height: 70px;
  margin: 20px;
}

.title-box {
  width: 340px;
  height: 70px;
  background-color: antiquewhite;
  border-radius: 10px;
}

.header {
  color: navy;
  font-family: cursive;
  text-align: center;
  font-size: xx-large;
  margin: auto;
}

.subheader {
  color: red;
  text-align: center;
  padding-bottom: 2vh;
  font-size: large;
  margin: auto;
}
